<template>
  <div class="body_context">
    <div class="button_context">
      <div class="img_context">
        <input id="file" type="file" accept="image/" capture="camera" >
        <img id="img" class="img" :src="pic"  height="200px" width="200px"/>
      </div>

      
      <button @click="sendRequest">AjaxRequest</button>
      <button @click="websocket">Websocket</button>
      <button @click="videoPage">video</button>

      <button
        v-for="(item, index) in buttonRes"
        :key="'123' + index"
        @click="actionApi(item.name, item.param)"
      >
        {{ item.name }}
      </button>
    </div>
  </div>
</template>

<script>
import { getActionsResponse, invokeApi } from "./YYHybridAction";
import {yy_hybrid} from '../YYConfig/YYJSbridge'
import axios from 'axios'

const axiosaa= axios.create({
  timeout: 5000, // 请求超时时间
  headers: { "content-type": "application/x-www-form-urlencoded" }
});
export default {
  name: "HybridAction",
  data() {
    return {
      buttonRes: getActionsResponse(),
    };
  },
  methods: {
    actionApi(action, params) {
      invokeApi(action, params);
    },

    sendRequest(){
      axiosaa.post('https://sanxia.zybang.com/sanxia/module/list',{params: {
        appId: 'homework',        // 参数 firstName
        osType: 'ios'    // 参数 lastName
      }}).then(response => (alert(response)))
      .catch(function (error) { // 请求失败处理
        console.log(error);
      });
    },
    selectImg:function(){
        document.getElementById('filed').click()
    },
    filePreview(e){
        let _this = this
        var files = e.target.files[0]
        if (!e || !window.FileReader) return  // 判断是否支持FileReader
        let reader = new FileReader()
        reader.readAsDataURL(files) // 文件转换
        reader.onloadend = function () {
          _this.pic = this.result
          _this.textHide=false;
        }
    },
    websocket(){
      yy_hybrid("socketGuid",
        {}
      );
    },
    videoPage(){
      var pageUrl = "http://"+window.location.host + "/audio.html";
      yy_hybrid("core_openWindow",
        {"pageUrl":pageUrl,"title":"video"}
      );
    }

  },
};
</script>

<style src="./YYHybridAction.css" scoped></style>
