var invokeDate = (new Date()).getTime();

export function getActionsResponse(){
    return ActionsResponse;
}

export function _callBack(response) {

    var allInvokeDate = (new Date()).getTime() - invokeDate;
    console.log("调用时间");
    console.log(allInvokeDate);
    window.localStorage.setItem("actionResponse",JSON.stringify(JSON.parse(JSON.stringify(response)),undefined, 2));
    window.localStorage.setItem("actionInvokeDate",allInvokeDate);

    var pageUrl = "http://"+window.location.host + "/response.html";
    yy_hybrid("core_openWindow",
        {"pageUrl":pageUrl,"title":"AAAAAA"}
    );
}

export function invokeApi(action, params){
    invokeDate = (new Date()).getTime();
    window.localStorage.setItem("actionName",action);
    if(JSON.stringify(params) == "{}"){
        window.localStorage.setItem("actionParams","无入参");
    }else{
        window.localStorage.setItem("actionParams",JSON.stringify(JSON.parse(JSON.stringify(params)),undefined, 2));
    }
    yy_hybrid(action, params, _callBack);
}